body {
  background-color: #f2f2f2;
  font-family: sans-serif;
}

p a {
  color: #333;
  border-bottom: 1px dotted #333;
  text-decoration: none;
}

.container {
  max-width: 600px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  display: flex;
}

button[data-start] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 20px;
}

button[data-stop] {
  color: #fff;
  cursor: pointer;
  background-color: #f44336;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 20px;
}

